body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0; margin: 0;
}
.App {
  text-align: center;
}


.header img{
  display: block;
  position: absolute;
  top: -140px; right: -40px;
  width: 300px;
  height: 300px;
  z-index: 0;
}

.mdl-layout__drawer-button{
  display: none !important;
}
.material-icons {
  color: white !important;
}
.mdl-layout__header-row {
  padding: 0 !important;
}
/** LATEST PAGE CONTENT **************************************/
.latestPageContent {
  display: grid;
  grid-template-columns: auto 210px;
  
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .latestPageContent {
    display: grid;
    grid-template-columns: 100%;
  }
} 
@media only screen and (max-width: 1024px) {
  .latestPageContent {
    margin-top: 0px;
  }
  .mdl-layout__drawer-button{
    display: block !important;
    position: absolute; top: -8px !important; left: 0;
  }
}

/** LEFT COLLUMN *********************************************/
.leftCollumn {
  display: grid;
  grid-template-columns: 50% 50% ;
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .leftCollumn {
    position: relative;
    grid-template-columns: 100% ;
    margin-top: 170px;
  }
  .apiRef {
    display: none;
  }
} 

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .leftCollumn {
    grid-template-columns: 100%;
  }
} 

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .leftCollumn {
    grid-template-columns: 50% 50% ;
  }
  
} 

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .leftCollumn {
    grid-template-columns: 33.3% 33.3% 33.3%;
  }
} 

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .leftCollumn {
    grid-template-columns: 25% 25% 25% 25%;
  }
}

/** RIGHT COLLUMN *********************************************/
.headlines {
  width: 200px;
}

.article {
    box-sizing: border-box;
    padding: 10px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    position: relative;
}
.article:nth-of-type(1) {
    grid-column-start: 1;
    grid-column-end: 2;
}
/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 1024px) {
    .article:nth-of-type(1) {
        grid-column-start: 1;
        grid-column-end: 3;
    }
    .article:nth-of-type(1):before {
        content: 'NEW';
        position: absolute;
        top: 15px; left: 15px;
        width: 70px;
        height: 40px;
        line-height: 40px;
        padding: 5px;
        background: red;
        color: white;
        border-radius: 10px;
        z-index: 10;
        font-size: 20px;
        font-weight: bold;
      }
    .article:nth-of-type(1) header{
    height: 300px;
  }
} 

.article header img{
    width: 100%;
    height: auto;
    display: none;
}
.article header {
    position: relative;
    display: block;
    height: 200px;
    background-size: cover;
}
.article header h5{
    position: absolute;
    padding: 10px 2%; margin: 0;
    width: 96%;
    height: auto;
    background: rgba(0,0,0,0.85);
    color: white;
    bottom: 20px;
    font-size: 16px;
}
.subheader {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.85);
    color: white;
    height: 20px;
    bottom: 0px;
}
.articleSource {
    position: absolute;
    left: 5px;
    font-size: 10px;
}
.articleTime {
    position: absolute;
    right: 5px;
    font-size: 10px;
}
.headline {
    position: relative;
    line-height: 50px;
}
.headline:before {
    content: '';
    position: absolute;
    top: 0; left: 60%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    width: 20%;
    height: 100%;
    padding-bottom: 10px;
    border-bottom: 1px solid rgba(0,0,0,0.9);
}
.headline p {
    margin-left: 39px;
    //border: 1px solid black;
}
.headline a {
    line-height: 10px;
    text-decoration: none;
    color: black;
}
.firstLetter {
    display: block;
    width: 40px; height: 40px;
    line-height: 40px;
    color: white;
    background: rgba(0,0,0,0.9);
    border-radius: 50%;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left:0;
    -webkit-transform: translate(0,-50%);
            transform: translate(0,-50%);
}
.headline:nth-child(even) {

   
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .headline {
      display: none;
    }
  } 
.calendar {
    margin-top: 10px;
    display: block;
    width: 200px;
    height: 200px;
}
.calendar span {
    display: block;
    font-size: 30px;
    
}
.calendar span:nth-child(1) {
    height: 40px;
    background: rgb(63,81,181);
    color: white;
    line-height: 40px;
}

.calendar span:nth-child(2) {
    height: 120px;
    line-height: 120px;
    background: rgba(0,0,0,0.9);
    font-size: 100px;
    color: white;
}
.calendar span:nth-child(3) {
    height: 40px;
    background: rgb(63,81,181);
    color: white;
    line-height: 40px;
}

@media only screen and (max-width: 600px) {
    .calendar {
      display: none;
    }
  } 
.rightCollumn select {
    position: relative;
    left: -5px;
    top: 10px;
    margin-bottom: 10px;
    box-sizing: border-box;
    width: 95%;
    padding: 5%;
  }

  .navigation ul {
    list-style-type: none;
    background: rgb(63,81,181);
    border-radius: 10px 0 0 10px;
    padding: 10px;
  }
  .navigation ul li{
    cursor: pointer;
    padding: 0 10px;
    color: white;
  }
  

  @media only screen and (max-width: 600px) {
    .rightCollumn {
      position: fixed;
      width: 100%;
      background: rgb(63,81,181);
      padding: 10px 0;
    }
    .rightCollumn select {
      position: relative;
      left: -5px;
      top: 10px;
      margin-bottom: 10px;
      box-sizing: border-box;
      width: 95%;
      padding: 5%;
    }
    .navigation ul {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      width: 100%;
    }
    .navigation ul li {
      border: 1px solid white;
      color: white;
      padding: 5px;
      border-radius: 5px;
      margin: 2px;
      background: rgb(63,81,181);
    }
    .rightCollumn select {
      width: 50%;
    }
  } 
.header {
    position: relative;
    top: 0;
    width: 100%;
    height: auto;
    line-height: 120px;
    background: rgb(63,81,181);
    padding: 0; margin: 0;
}
.header h1 {
    line-height: 120px;
    color: white;
}
@media only screen and (max-width: 769px) {
    .header h1 {
      font-size: 30px;
    }
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .header {
      display: none;

    }
} 
